import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import 'rxjs/add/operator/switchMap';


import {ITEMS} from '../items';
import {Item} from '../item';

@Component({
  selector: 'app-store-item',
  templateUrl: './store-item.component.html',
  styleUrls: ['./store-item.component.css']
})
export class StoreItemComponent implements OnInit {
  itemId:number;
  item: Item;
  private sub: any;

  defaultSelection = {value:null, name:'default'};
  selectedOption = this.defaultSelection;

  variableBuyButtonDisabled = true;

  constructor(
  private route: ActivatedRoute,
  private router: Router
  ) {}

  ngOnInit() {

    this.sub = this.route.params.subscribe(params => {
       this.itemId = +params['itemId']; // (+) converts string 'id' to a number
       // In a real app: dispatch action to load the details here.
      this.item = this.getItem(this.itemId);
      console.log(this.item);
    });

  }

  getItem(itemId): Item{
    // console.log("getItem# "+itemId)
    // return ITEMS.find(this.findItem);
    for( var i=0; i<ITEMS.length; i++){
      if(ITEMS[i].id === itemId){
        return ITEMS[i];
      }
    }
    return null;
  }




  onchange(option){

    if(this.selectedOption == this.defaultSelection) {
      // console.log('Disabling button')
      this.variableBuyButtonDisabled = true;
    } else {
      // console.log('Enabling button')
      this.variableBuyButtonDisabled = false;
    }
  }

}



