import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import {HomepageComponent} from './homepage/homepage.component'
import {NotFoundComponent} from './not-found/not-found.component'
import {AboutComponent} from './about/about.component'
import {FaqComponent} from './faq/faq.component'
import {BookNowComponent} from './book-now/book-now.component'
import {BoatingComponent} from './boating/boating.component'
import {KayakingComponent} from './kayaking/kayaking.component'
import {StoreComponent} from './store/store.component'
import {StoreItemComponent} from './store/store-item/store-item.component'

const routes: Routes = [
  { path: '', component: HomepageComponent },
  { path: '404', component: NotFoundComponent},
  { path: 'about', component: AboutComponent},
  { path: 'faq', component: FaqComponent},
  { path: 'book', component: BookNowComponent},
  { path: 'inshore-fishing', component: BoatingComponent},
  { path: 'kayaking', component: KayakingComponent},
  { path: 'store', component: StoreComponent},
  { path: 'store/:itemId', component: StoreItemComponent},
  { path: '**', redirectTo: '404'}
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}



